export default "header {\n  width: 100%;\n  height: 7em;\n  padding-top: 0.3em;\n  border-top: 4px solid black;\n  background-color: #5d0c1d;\n}\n\n/* img {\n  width: 406px;\n  height: 104px;\n} */\n\nheader {\n  display: flex;\n  justify-content: center;\n}\n\n.nav-item > .nav-link {\n  color: white;\n}\n\n/* .flex-container nav {\n  justify-content: space-between;\n} */\n\n/* The navigation menu */\n\n.navbar-expand-md {\n  /* overflow: hidden; */\n  background-color: #333;\n  border-top: 4px solid black;\n  border-bottom: 4px solid black;\n  z-index: 999;\n  /* justify-content: space-between; */\n}\n\n.mx-auto {\n  display: block;\n}\n\n.nav-link {\n  background-color: #333;\n  color: #ffffff;\n  font-size: 16px;\n  text-align: center;\n  padding: 14px 16px;\n  text-decoration: none;\n  border: none;\n  outline: none;\n}\n\n/* Navigation links */\n\n.navbar a {\n  /* float: left; */\n  font-size: 16px;\n  color: #ffffff;\n  text-align: center;\n  padding: 14px 16px;\n  text-decoration: none;\n  z-index: 999;\n}\n\n/* a,\nbutton {\n  list-style-type: none;\n  display: none;\n} */\n\n/* The subnavigation menu */\n\n.subnav,\n.nav-item {\n  position: relative;\n  display: inline-block;\n}\n\n/* Subnav button */\n\n.subnav .subnavbtn,\n.dropdown-menu {\n  font-size: 16px;\n  border: none;\n  outline: none;\n  color: #ffffff;\n  padding: 14px 16px;\n  background-color: inherit;\n  font-family: inherit;\n  margin: 0;\n}\n\n.dropdown-menu {\n  font-size: 16px;\n  border: none;\n  outline: none;\n  padding: 14px 16px;\n  background-color: inherit;\n  font-family: inherit;\n  margin: 0;\n}\n\n/* button {\n  color: #ffffff;\n} */\n\n.navbar a:hover,\n.navbar .nav-link:hover,\nbutton:hover {\n  background-color: #d6d6d6;\n  color: black;\n}\n\n.dropdown-item:hover {\n  background-color: #d6d6d6;\n  color: black;\n}\n\n.nav-item:hover .dropdown-toggle {\n  background-color: #d6d6d6;\n  color: black;\n}\n\n/* .navbar-toggler-icon {\n  border: 0px;\n} */\n\n.fa-bars {\n  color: #d6d6d6;\n  padding: 5px 10px;\n}\n\n.fa-home {\n  color: black;\n  font-size: 180%;\n  padding-left: 8px;\n  padding-right: 8px;\n}\n\n.fa-bars:hover {\n  background-color: #d6d6d6;\n  color: black;\n  border: 4px solid #d6d6d6;\n}\n\n/* Style the subnav content */\n\n.subnav-content {\n  display: none;\n  overflow: hidden;\n  margin-left: 0px;\n  background-color: #d6d6d6;\n  color: black;\n  position: absolute;\n  min-width: 160px;\n  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\n}\n\n.dropdown-menu {\n  /* display: none;\n  overflow: hidden; */\n  text-align: left;\n  text-decoration: none;\n  margin-left: 0px;\n  padding-left: 0px;\n  background-color: #d6d6d6;\n  color: black;\n  position: absolute;\n  min-width: 100%;\n  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\n}\n\n/* Style the subnav links */\n\n.subnav-content a,\n.dropdown-menu .dropdown-item {\n  float: left;\n  width: 100%;\n  background-color: #d6d6d6;\n  color: black;\n  text-align: left;\n  text-decoration: none;\n}\n\n/* Add a dark background color on hover */\n\n.subnav-content a:hover,\n.nav-item .dropdown-menu .dropdown-item:hover {\n  background-color: #333;\n  text-align: left;\n  width: 100%;\n  color: #ffffff;\n}\n\n/* .nav-item .dropdown-menu .dropdown-item .show:hover {\n  background-color: #d6d6d6;\n  color: black;\n} */\n\n.dropdown-menu.hover-state {\n  display: block;\n}\n\n/* .navbar-dark .navbar-nav .nav-link.hover-state {\n  color: red;\n} */\n\n.navbar-dark .navbar-nav .nav-link:hover {\n  color: black;\n}\n\n/* .dropdown-submenu {\n  position: relative;\n}\n\n.dropdown-submenu a::after {\n  transform: rotate(-90deg);\n  position: absolute;\n  right: 6px;\n  top: 0.8em;\n}\n\n.dropdown-submenu .dropdown-menu {\n  top: 0;\n  left: 100%;\n  margin-left: 0.1rem;\n  margin-right: 0.1rem;\n} */\n\n/* .navbar-toggle {\n  position: absolute;\n  top: 10px;\n  left: 20px;\n  cursor: pointer;\n  font-size: 24px;\n}\n\n.active {\n  display: block;\n}\n\nfa-bars {\n  color: #d6d6